document.querySelectorAll('input[name="married"]').forEach(radio => {
    radio.addEventListener('click', function() {
        document.querySelectorAll('input[name="married"]:not(:checked)').forEach(radio => {
            radio.disabled = true;
        });
    });
});

document.getElementById('survey-form').addEventListener('submit', function(event) {
    event.preventDefault();
    const married = document.querySelector('input[name="married"]:checked').value;
    if (married === 'yes') {
        alert('ДОСТУП ЗАБЛОКИРОВАН');
    } else {
        alert('Можете купить билет');
        document.querySelector('.contact-us').classList.remove('disabled');
    }
    document.querySelector('#submit-btn').classList.add('disabled');
});